* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0;
}

.header {
  color: #fff;
  text-align: center;
  background: linear-gradient(135deg, #007bff, #3adc98);
  width: 100%;
  padding: 2rem 0;
  position: relative;
}

.header h1 {
  margin: 0;
  font-size: 32px;
}

.header p {
  opacity: .9;
  font-size: 1.1rem;
}

#btn-info, #btn-close {
  position: absolute;
  top: 12px;
  right: 12px;
}

#btn-info svg, #btn-close svg {
  width: 20px;
}

#info-content {
  z-index: 9999999;
  opacity: 0;
  background: #fff;
  width: 100vw;
  max-width: 768px;
  height: 100vh;
  padding: 40px;
  transition: top .3s;
  position: fixed;
  top: -100%;
  left: 50%;
  overflow: auto;
  transform: translate(-50%);
  box-shadow: 0 4px 6px -1px #0000001a, 0 2px 4px -1px #0000000f;
}

#info-content h2 {
  text-align: center;
  color: #007bff;
  margin-bottom: 16px;
}

#info-content p {
  margin-bottom: 8px;
  font-weight: 700;
}

#info-content ul {
  margin-bottom: 24px;
}

#info-content ul li {
  font-size: 14px;
  line-height: 1.6;
}

#info-content h4 {
  color: #007bff;
  padding: 8px 0 0;
  font-family: 辰宇落雁體 Thin;
  font-size: 24px;
  font-weight: 300;
  line-height: 1.2;
}

.open #info-content {
  opacity: 1;
  top: 0;
}

.main-content {
  width: 100%;
  padding: 0;
}

.sticky-top .main-content {
  padding-top: 48px;
}

.tabs {
  width: 100%;
}

.tabs .tab-list {
  scrollbar-width: none;
  -ms-overflow-style: none;
  max-width: 768px;
  display: flex;
  overflow-x: auto;
  box-shadow: 0 4px 6px -1px #0000001a, 0 2px 4px -1px #0000000f;
}

.tabs .tab-list::-webkit-scrollbar {
  display: none;
}

@media (width <= 768px) {
  .tabs .tab-list {
    max-width: 100vw;
  }
}

.tabs .tab-trigger {
  cursor: pointer;
  white-space: nowrap;
  background: #fff;
  border: none;
  border-right: 1px solid #d9e3f0;
  width: 100%;
  padding: 8px 24px 6px;
  font-size: 14px;
  line-height: 1.4;
  transition: all .2s;
}

.tabs .tab-trigger.active {
  color: #007bff;
  border-bottom: 2px solid #007bff;
  font-weight: 500;
}

.tabs .tab-trigger:hover:not(.active) {
  background: #ffffffb3;
}

.tabs .tab-trigger .tab-date {
  color: #6f7d91;
  font-size: 12px;
  line-height: 1;
  display: block;
}

.sticky-top .tab-list {
  z-index: 1000;
  background: #fff;
  position: fixed;
  top: 0;
  left: 50%;
  transform: translate(-50%);
}

@media (width >= 768px) {
  .sticky-top .tab-list.tab-list .tab-trigger {
    padding: 12px 24px;
  }
}

.tab-content {
  padding: 40px;
  display: none;
}

@media (width <= 768px) {
  .tab-content {
    padding: 16px;
  }
}

.tab-content.active {
  display: block;
}

.tab-content .day-info {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.tab-content .day-info .title {
  color: #007bff;
  font-weight: 500;
}

@media (width >= 768px) {
  .tab-content .day-info .title {
    font-size: 20px;
  }
}

.tab-content .day-info .date {
  color: #6f7d91;
  font-size: 12px;
}

.tab-content .day-note {
  color: #6f7d91;
  margin-top: 4px;
  font-size: 14px;
  line-height: 1.4;
}

.timeline {
  padding: 16px 0 0 16px;
  position: relative;
}

.timeline .timeline-item {
  margin-bottom: 8px;
  position: relative;
}

.timeline .timeline-item .time-badge {
  color: #007bff;
  text-align: right;
  font-size: 12px;
  position: absolute;
  top: 4px;
  left: -16px;
}

.timeline .timeline-item .timeline-dot {
  z-index: 10;
  background: #007bff;
  border: 4px solid #fff;
  border-radius: 50%;
  width: 16px;
  height: 16px;
  position: absolute;
  top: 6px;
  left: 20px;
  box-shadow: 0 10px 30px -10px #3b82f64d;
}

.timeline .timeline-item .timeline-line {
  background: #d9e3f0;
  width: 2px;
  height: calc(100% + 20px);
  position: absolute;
  top: 8px;
  left: 27px;
}

.timeline .timeline-item:last-child .timeline-line {
  display: none;
}

.timeline .timeline-item .timeline-content {
  margin-left: 48px;
  padding-top: 12px;
}

.timeline-content .travel-card {
  cursor: pointer;
  background: #fff;
  border: 1px solid #d9e3f0;
  border-radius: 12px;
  margin-bottom: 1rem;
  transition: all .3s;
  box-shadow: 0 4px 6px -1px #0000001a, 0 2px 4px -1px #0000000f;
}

.timeline-content .travel-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 30px -10px #3b82f64d;
}

.timeline-content .travel-card:hover .titles h3 {
  color: #007bff;
}

.timeline-content .travel-card:hover .image-overlay {
  background: #0000001a;
}

.timeline-content .travel-card .card-content {
  flex-direction: row;
  gap: 1rem;
  padding: 1rem;
  display: flex;
}

.timeline-content .travel-card .image-container {
  cursor: pointer;
  border-radius: 8px;
  flex-shrink: 0;
  width: 6rem;
  height: 4rem;
  transition: transform .2s;
  position: relative;
  overflow: hidden;
}

.timeline-content .travel-card .image-container:hover {
  transform: scale(1.05);
}

.timeline-content .travel-card .image-container .travel-image {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.timeline-content .travel-card .text-content {
  flex: 1;
  min-width: 0;
}

.timeline-content .travel-card .text-content .card-header {
  justify-content: between;
  flex-direction: row;
  align-items: flex-start;
  display: flex;
}

.timeline-content .travel-card .text-content .card-header .titles {
  flex: 1;
}

.timeline-content .travel-card .text-content .card-header .titles h3 {
  color: #050d1a;
  margin-bottom: 0;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.4;
  transition: color .2s;
}

.timeline-content .travel-card .text-content .card-header .titles p {
  color: #6f7d91;
  margin-top: 4px;
  font-size: 14px;
  line-height: 1.2;
}

.timeline-content .travel-card .text-content .card-header .expand-btn {
  color: #6f7d91;
  transition: color .2s;
  display: flex;
}

.timeline-content .travel-card .text-content .card-header .expand-btn:hover {
  color: #007bff;
}

.timeline-content .travel-card .text-content .card-header .expand-btn svg {
  width: 16px;
  height: 26px;
  transition: transform .2s;
  transform: rotate(-90deg);
}

.timeline-content .travel-card .text-content .card-header .expand-btn.expanded svg {
  transform: rotate(90deg);
}

.timeline-content .travel-card .expanded-content {
  border-top: 1px solid #d9e3f0;
  margin-top: .75rem;
  padding-top: .75rem;
  font-size: 14px;
  animation: .2s ease-out slideDown;
  display: none;
}

.timeline-content .travel-card .expanded-content.show {
  flex-direction: column;
  align-items: start;
  gap: 8px;
  display: flex;
}

.timeline-content .travel-card .expanded-content a:not(.map-link) {
  color: #007bff;
  text-decoration: underline;
}

.timeline-content .travel-card .expanded-content a.map-link {
  background-color: #e6f2ff;
  border-radius: 10000000px;
  padding: 4px 12px 4px 8px;
  font-size: 12px;
}

.timeline-content .travel-card .expanded-content a.map-link svg {
  width: 16px;
  height: 16px;
  position: relative;
  top: 2.5px;
}

.timeline-content .transportation {
  color: #6f7d91;
  gap: .75rem;
  font-size: 12px;
  display: flex;
}

.modal {
  z-index: 1000;
  background-color: #000c;
  width: 100%;
  height: 100%;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.modal.show {
  display: block;
}

.modal .modal-content {
  background: #fff;
  border-radius: 8px;
  max-width: 90%;
  max-height: 90%;
  position: relative;
  top: 50%;
  left: 50%;
  overflow: hidden;
  transform: translate(-50%, -50%);
}

.modal .close {
  color: #fff;
  cursor: pointer;
  z-index: 10;
  background: #00000080;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 2.5rem;
  height: 2.5rem;
  font-size: 2rem;
  font-weight: bold;
  transition: background .2s;
  display: flex;
  position: absolute;
  top: 1rem;
  right: 1rem;
}

.modal .close:hover {
  background: #000c;
}

.modal #modalImage {
  width: 100%;
  height: auto;
  display: block;
}

@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(50px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@media (width <= 768px) {
  .header h1 {
    font-size: 2rem;
  }

  .card-content {
    flex-direction: column;
    gap: .75rem;
  }

  .image-container {
    width: 100%;
    height: 8rem;
  }

  .card-header {
    flex-direction: column;
    gap: .5rem;
  }

  .expand-btn {
    align-self: flex-start;
    margin-left: 0;
  }
}
